import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot } from '@angular/router';
import { ApplicationService } from '@c8y/client';
import { Permissions } from '@c8y/ngx-components';

@Injectable()
export class SmsGatewayGuard implements CanActivate {
  private static readonly applicationName = 'sms-gateway';
  private showSMSProvider: Promise<boolean>;

  constructor(private permissions: Permissions, private applicationService: ApplicationService) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<boolean> {
    if (this.showSMSProvider === undefined) {
      const hasRole: Promise<boolean> = Promise.resolve(this.permissions.hasRole('ROLE_SMS_READ'));

      this.showSMSProvider = hasRole.then(
        (hasPermissions: boolean) =>
          hasPermissions &&
          this.applicationService
            .isAvailable(SmsGatewayGuard.applicationName)
            .then(result => result && result.data)
      );
    }

    return this.showSMSProvider;
  }
}
